.App {
  text-align: center;
}

#machine-container {
  padding: 1%;
}

#machine-keys {
  display: grid;
  grid-template-rows: 60px 60px 60px;
  grid-template-columns: 60px 60px 60px;
  grid-gap: 10px;
  padding-left: 62%;
}

#keys-info {
  display: grid;
  grid-template-rows: 50% 50%;
  grid-template-columns: 50% 50%;
}

#display-info {
  padding-right: 56%;
}

#on-or-off {
  padding: 1%
}


